import React, { FC } from 'react';
import RHFInput from '@components/RHF/RHFInput';
import RHFRadio from '@components/RHF/RHFRadio';
import { ErrorText } from '@components/ReusedComponents/Input/style';
import { ADD_ORDER_REGISTRATION_CART } from '@components/Cart/OrderRegistration/validations';
import {
    HeadTitle,
    Container,
    Content,
    Subtitle,
    GridPersonalData,
    GridAddress,
    ContainerDelivery,
    ContainerPayment,
    ContentPayment,
    ContentDelivery,
} from './style';

const personalData = [
    { name: ADD_ORDER_REGISTRATION_CART.NAME, placeholder: ' *' },
    { name: ADD_ORDER_REGISTRATION_CART.SURNAME, placeholder: ' *' },
    { name: ADD_ORDER_REGISTRATION_CART.EMAIL, placeholder: ' e-mail*' },
    { name: ADD_ORDER_REGISTRATION_CART.PHONE, placeholder: ' *' },
];
const address = [
    { name: ADD_ORDER_REGISTRATION_CART.CITY, placeholder: '*' },
    { name: ADD_ORDER_REGISTRATION_CART.POSTOFFICE, placeholder: ' *' },
];
const delivery = [
    { value: 'pickup', label: ' - .   14(.  )' },
    { value: 'russianPost', label: ' / 1-3  / 30$' },
    { value: 'newMail', label: ' ' },
    { value: 'DHL', label: 'DHL / 3-7  / 60$' },
];
const payment = [
    { value: 'prepayment', label: '    24' },
    { value: 'Visa/MasterCard', label: '   Visa/MasterCard' },
    { value: 'cashMail', label: '     ' },
];
interface IForm {
    errors?: any;
}
const OrderRegistration: FC<IForm> = ({ errors }) => (
    <Container>
        <Content>
            <HeadTitle> </HeadTitle>
            <Subtitle> :</Subtitle>
            <GridPersonalData>
                {personalData.map((data) => (
                    <RHFInput key={data.name} name={data.name} placeholder={data.placeholder} />
                ))}
            </GridPersonalData>
            <Subtitle> :</Subtitle>
            <ContainerDelivery>
                <ContentDelivery>
                    <Subtitle> :</Subtitle>
                    <Subtitle> :</Subtitle>
                    {delivery.map((item) => (
                        <RHFRadio
                            key={item.value}
                            name={ADD_ORDER_REGISTRATION_CART.DELIVERY}
                            value={item.value}
                            label={item.label}
                        />
                    ))}
                </ContentDelivery>
                {errors?.[ADD_ORDER_REGISTRATION_CART.PAYMENT] && (
                    <ErrorText>{errors[ADD_ORDER_REGISTRATION_CART.DELIVERY]?.message}</ErrorText>
                )}
            </ContainerDelivery>
            <Subtitle> :</Subtitle>
            <GridAddress>
                {address.map((data) => (
                    <RHFInput key={data.name} name={data.name} placeholder={data.placeholder} />
                ))}
            </GridAddress>
            <Subtitle>        :</Subtitle>
            <ContainerPayment>
                <ContentPayment>
                    {payment.map((item) => (
                        <RHFRadio
                            key={item.value}
                            name={ADD_ORDER_REGISTRATION_CART.PAYMENT}
                            value={item.value}
                            label={item.label}
                        />
                    ))}
                </ContentPayment>
                {errors?.[ADD_ORDER_REGISTRATION_CART.PAYMENT] && (
                    <ErrorText>{errors[ADD_ORDER_REGISTRATION_CART.DELIVERY]?.message}</ErrorText>
                )}
            </ContainerPayment>
        </Content>
    </Container>
);
export default OrderRegistration;
