import * as yup from 'yup';

export enum ADD_ORDER_REGISTRATION_CART {
    NAME = 'name',
    SURNAME = 'surname',
    EMAIL = 'email',
    PHONE = 'phone',
    CITY = 'city',
    POSTOFFICE = 'postOffice',
    DELIVERY = 'delivery',
    PAYMENT = 'payment',
}
const phoneRegExp = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/;
export const orderRegistration = yup.object().shape({
    [ADD_ORDER_REGISTRATION_CART.NAME]: yup.string().required(' '),
    [ADD_ORDER_REGISTRATION_CART.SURNAME]: yup.string().required(' '),
    [ADD_ORDER_REGISTRATION_CART.EMAIL]: yup.string().required(' ').email('  '),
    [ADD_ORDER_REGISTRATION_CART.PHONE]: yup
        .string()
        .required('  ')
        .matches(phoneRegExp, '    '),
    [ADD_ORDER_REGISTRATION_CART.CITY]: yup.string().required(' '),
    [ADD_ORDER_REGISTRATION_CART.POSTOFFICE]: yup.string().required('  '),
    [ADD_ORDER_REGISTRATION_CART.DELIVERY]: yup.string().required('  '),
    [ADD_ORDER_REGISTRATION_CART.PAYMENT]: yup.string().required('  '),
});
